<template>
  <div style="display: inline-block">
    <Button @click="insert" size="small"></Button>
    <Modal
      v-model="showModal"
      title=""
      @on-ok="insertSvgFile"
      @on-cancel="(showModal = false), (jsonFile = null)"
    >
      <Upload :before-upload="handleUpload" action="#">
        <Button icon="ios-cloud-upload-outline">JSON</Button>
      </Upload>
    </Modal>
  </div>
</template>

<script>
import select from '@/mixins/select'
import FontFaceObserver from 'fontfaceobserver'
export default {
  name: 'ToolBar',
  mixins: [select],
  data() {
    return {
      showModal: false,
      jsonFile: null,
    };
  },
  methods: {
    insert() {
      this.svg = ''
      this.showModal = true
    },
    insertSvgFile() {
      if (!this.jsonFile) {
        this.$Message.error('')
        return
      }
      // 
      this.getFonts(this.jsonFile).then(() => {
        this.canvas.c.loadFromJSON(this.jsonFile, this.canvas.c.renderAll.bind(this.canvas.c));
      })
    },
    // 
    getFonts(jsonFile) {
      const skipFonts = ['arial', 'Microsoft YaHei']
      const fontFamilys = JSON.parse(jsonFile).objects.filter(item => {
        // text 
        return (item.type.includes('text') && !skipFonts.includes(item.fontFamily))
      }).map(item => item.fontFamily)
      const fontFamilysAll = fontFamilys.map(fontName => {
        const font = new FontFaceObserver(fontName);
        return font.load(null, 150000)
      })
      return Promise.all(fontFamilysAll)
    },
    handleUpload(file) {
      const reader = new FileReader();
      reader.readAsText(file, 'UTF-8');
      reader.onload = () => {
        this.jsonFile = reader.result
      };
      return false;
    },
  }
};
</script>

